#component {
    padding: 3%;
    width: 100vw;
    position: relative;
}

.center {
    font-family: Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    text-align: center;
    font-size: 1.1em;
}

.footerLinks {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center !important;
        justify-content: center !important;
}

.icon {
    font-size: 1.5em;
    width: 40px;
    height: 40px;
    padding: 20%;
    color: #55769A;
}

.icon:hover {
    text-decoration: none;
    color: #b1769b;
}

/**
    increase the footer size on an iPad
 */
@media (width: 1024px) {
    #component {
        height: 25vh;
    }

    #footerContent {
        padding: 7% 0px 7% 0px;
    }
}


